import CommonHeader from '@/components/layouts/CommonHeader.vue'
import graceFullLoading from "@/graceUI/components/graceFullLoading.vue"
import graceStar from '@/graceUI/components/graceStar.vue'

export default {
	components: {
		CommonHeader,
		graceFullLoading,
		graceStar
	},
	data() {
		return {
			loading: true,
			info: {},
			list: [],
			currentOrder: 0,
			orderType: 0,
		}
	},
	onLoad(e) {
		this.getInfo(e.id)
		this.getList(e.id)
	},
	methods: {
		collect() {
			uni.request({
				url: getApp().globalData.api + 'store/collect',
				method: 'POST',
				data: {
					open_id: uni.getStorageSync('open_id'),
					id: this.info.id,
					status: this.info.is_collect == 1 ? 0 : 1
				},
				header: {
					'content-type': 'application/json' //自定义请求头信息
				},
				success: (res) => {
					this.info.is_collect = this.info.is_collect == 1 ? 0 : 1;
				}
			});
		},
		toEvalues(store_id) {
			uni.navigateTo({
				url: '/pages/product/ratings?store_id=' + store_id
			})
		},
		changeOrder(current) {
			let type = this.orderType
			if (current == this.currentOrder) {
				if (type == 0) {
					this.currentOrder = current
					this.orderType = 2
				} else if (type == 2) {
					this.currentOrder = current
					this.orderType = 1
				} else if (type == 1) {
					this.currentOrder = 0
					this.orderType = 0
				}
			} else {
				this.currentOrder = current
				this.orderType = 2
			}

			this.getList(this.info.id)
		},
		toProduct(id) {
			uni.navigateTo({
				url: "/pages/product/offline_product?id=" + id
			})
		},
		getList(id) {
			uni.request({
				url: getApp().globalData.api + 'store/product-list',
				method: 'GET',
				data: {
					open_id: uni.getStorageSync('open_id'),
					id: id,
					order: this.currentOrder,
					order_type: this.orderType
				},
				header: {
					'content-type': 'application/json' //自定义请求头信息
				},
				success: (res) => {
					this.list = res.data.data
				},
				fail: (res) => {

				}
			});
		},
		collect() {
			uni.request({
				url: getApp().globalData.api + 'store/collect',
				method: 'POST',
				data: {
					open_id: uni.getStorageSync('open_id'),
					id: this.info.id,
					status: this.info.is_collect == 1 ? 0 : 1
				},
				header: {
					'content-type': 'application/json' //自定义请求头信息
				},
				success: (res) => {
					if (this.info.is_collect == 1) {
						this.info.is_collect = 0
						this.info.collect -= 1
					} else {
						this.info.is_collect = 1
						this.info.collect += 1
					}
				},
				fail: (res) => {

				}
			});
		},
		getInfo(id) {
			uni.request({
				url: getApp().globalData.api + 'store/info',
				method: 'GET',
				data: {
					open_id: uni.getStorageSync('open_id'),
					id: id
				},
				header: {
					'content-type': 'application/json' //自定义请求头信息
				},
				success: (res) => {
					this.info = res.data.data
					this.loading = false
				},
				fail: (res) => {
					this.loading = false
				}
			});
		}
	}
}
